.App {
  text-align: center;
}
.header {
  width: 500px;
  margin-left: auto;
  margin-right: auto;
  background-color: aquamarine;
  font-size: large;
  font-weight: bold;
}
.slangItem {
  border-bottom: gray 1px solid;
  text-decoration: none;
  cursor: pointer;
}
.slangItem:hover {
  text-decoration: underline;
}
.content {
  width: 500px;
  margin-left: auto;
  margin-right: auto;
  border: 1px solid  #000;
}
input {
  width: 99%;
  border: 0;
  padding: 2.5px;
  line-height: 59px;
  border-bottom: 1px solid;
}